import { fetchGenreList } from './fetchGenreList';
import { handleResponse } from './galleryBuilder';
import { fetchPopularMovies } from './fetchMovieApi';

// Funcie pentru popularea paginii in main
// Funcie pentru popularea paginii n main
const showPage = async (page, isSearch = false, searchQuery = '') => {
  try {
    const genreList = await fetchGenreList();
    let response;

    if (isSearch) {
      response = await fetchMovies(searchQuery, page);
      handleResponse(response, true, genreList); // Se terge coninutul galeriei cnd se face o cutare
    } else {
      response = await fetchPopularMovies(page);
      handleResponse(response, page === 1, genreList); // Se menine coninutul existent al galeriei pentru paginile urmtoare
    }
  } catch (error) {
    console.error('Error', error);
  }
};

export { showPage };
